@import url("https://fonts.googleapis.com/css?family=Lato|Poppins:600,700");
html, body {
  margin: 0;
  width: 100%;
  color: #fff;
}

p {
  font-family: "Lato";
  font-weight: 500;
}

h1 {
  font-family: "Poppins";
  font-weight: 600;
}

.App {
  width: 100vw;
  height: 100vh;
}
.App .left {
  left: 10.1vw;
  z-index: 10;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#051937), color-stop(#0c1e42), color-stop(#14234e), color-stop(#1e2859), to(#292d64));
  background-image: -webkit-linear-gradient(top, #051937, #0c1e42, #14234e, #1e2859, #292d64);
  background-image: -o-linear-gradient(top, #051937, #0c1e42, #14234e, #1e2859, #292d64);
  background-image: linear-gradient(to bottom, #051937, #0c1e42, #14234e, #1e2859, #292d64);
}
.App .center {
  left: calc(50vw - 20vw/2);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#051937), color-stop(#0b224b), color-stop(#162b60), color-stop(#253374), to(#363b89));
  background-image: -webkit-linear-gradient(top, #051937, #0b224b, #162b60, #253374, #363b89);
  background-image: -o-linear-gradient(top, #051937, #0b224b, #162b60, #253374, #363b89);
  background-image: linear-gradient(to bottom, #051937, #0b224b, #162b60, #253374, #363b89);
  z-index: 100 !important;
}
.App .right {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#051937), color-stop(#0c1e42), color-stop(#14234e), color-stop(#1e2859), to(#292d64));
  background-image: -webkit-linear-gradient(top, #051937, #0c1e42, #14234e, #1e2859, #292d64);
  background-image: -o-linear-gradient(top, #051937, #0c1e42, #14234e, #1e2859, #292d64);
  background-image: linear-gradient(to bottom, #051937, #0c1e42, #14234e, #1e2859, #292d64);
  left: calc(80vw - 20vw/2);
  z-index: 30;
}
.App .block {
  cursor: pointer;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 20vw;
  height: 45vh;
  position: absolute;
  padding: 10px;
  top: calc(50vh - 22.5vh);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-transition: all 0.9s, outline 0.3s;
  -o-transition: all 0.9s, outline 0.3s;
  transition: all 0.9s, outline 0.3s;
}
.App .block .text {
  position: relative;
  top: -40px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  -ms-flex-align: center;
      align-items: center;
}
.App .block .text h1 {
  -webkit-transition: all 0.9s;
  -o-transition: all 0.9s;
  transition: all 0.9s;
  font-size: 3em;
  letter-spacing: 0.3vw !important;
}
.App .block .text p {
  -webkit-transition: all 0.9s;
  -o-transition: all 0.9s;
  transition: all 0.9s;
  font-size: 1.5em;
  padding: 0 10%;
}
.App .block:hover {
  outline: 3px dashed red;
}
.App .covering {
  z-index: 60;
}
.App .center-focused {
  padding: 20px !important;
  left: calc(50vw - 20vw) !important;
  top: calc(50vh - 45vh) !important;
  width: calc(20vw *2) !important;
  height: calc(45vh *2) !important;
  z-index: 100;
}
.App .center-focused h1 {
  font-size: 6em !important;
  letter-spacing: 0.6vw !important;
}
.App .center-focused p {
  font-size: 3em !important;
}



